import React, {useState, useEffect} from 'react';
import Axios from 'axios'
import ReactDOM from 'react-dom/client';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import './style.css';
import App from './app';
import Navbar from './navbar';
// import MenuItem from './getMenuItem'
import Header from './header'
import Footer from './footer'
import Port from './port'

function PageLoad (){
  const [data, setData] = useState([])
  const [page, setPage ] = useState('Home')

  useEffect(() => {
    Axios.get('https://astute-baton-362318.ue.r.appspot.com/api/json/')
        .then((resp) => setData(resp.data));
  }, [])

  // stops the code from running until the api has been called and state has received it's data
  if(data.length === 0) {
    return (
      <div className='d-flex justify-content-center align-items-center' style={{height: '100vh'}}>
      <h1>WAIT A  SECOND</h1>
      </div>
        )
  }
  
  const menus = ['Home', 'Breakfast', 'Lunch', 'Dinner', 'Dessert', 'Appetizer', 'Side']

  

  function setText(text) { //! explain this in demo
    setPage(text);
  }

return (
  <>
  <div className='container'>
    <Header />
    <div className="row justify-content-center d-flex align-items-center">
      {menus.map((menuItem) => (
        <Navbar text={menuItem} handleClick={setText} /> //! demo this
      ))}
    </div>
    <div className='container-fluid pt-4'>
      <Port data={data} page={page}/> //! also explain this
    </div>
    
    </div>
      
  </>
)
}

// todo can always port this function ^ to app and run through app
const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <div  >
  <PageLoad />
  </div>
)
